@import url(https://fonts.googleapis.com/css?family=Nunito);
.App {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
}

.main {
  height: 100vh;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

header {
  background: transparent;
  text-align: center;
  position: sticky;
  z-index: 999;
  width: 100%;
  height: 4rem;
}

.logo {
  margin: auto 0;
}

.nav-toggle {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

.nav-toggle-label {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 1em;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  display: block;
  background: black;
  height: 2px;
  width: 2em;
  border-radius: 2px;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after {
  content: "";
  position: absolute;
}

.nav-toggle-label span::before {
  bottom: 7px;
}

.nav-toggle-label span::after {
  top: 7px;
}

nav {
  position: absolute;
  text-align: left;
  top: 100%;
  left: 0;
  background: transparent;
  width: 100%;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  -webkit-transform-origin: top;
          transform-origin: top;
  transition: -webkit-transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 1em;
}

nav a {
  color: white;
  text-shadow: 1px 1px 2px rgba(150, 150, 150, 0.37);
  font-weight: bold;
  text-decoration: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}

nav a:hover {
  color: darkgray;
}

.nav-toggle:checked ~ nav {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  background-color: black;
}

.nav-toggle:checked ~ nav a {
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

@media screen and (min-width: 800px) {
  .nav-toggle-label {
    display: none;
  }

  nav a:hover {
    color: black;
  }

  header {
    display: grid;
    grid-template-columns: 1fr auto minmax(600px, 3fr) 1fr;
  }

  .logo {
    grid-column: 2 / 3;
  }

  nav {
    position: relative;
    text-align: left;
    transition: none;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    background: none;
    top: auto;
    top: initial;
    left: auto;
    left: initial;
    grid-column: 3 / 4;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  nav ul {
    display: flex;
  }

  nav li {
    margin-left: 3em;
    margin-bottom: 0;
  }

  nav a {
    opacity: 1;
    position: relative;
  }

  nav a::before {
    content: "";
    display: block;
    height: 2px;
    background: black;
    position: absolute;
    top: 0em;
    left: 0;
    right: 0;
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
    transition: -webkit-transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms;
  }

  nav a:hover::before {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

.data-block {
  overflow: scroll;
}

.data-block__title {
  border-bottom: 1px solid #fafafa;
  font-size: 0.8rem;
}

.data-block__content-list {
  width: 100%;
}

.data-block__content-row {
  border-bottom: 1px solid #ddd;
  padding: 0.25rem;
  font-size: 0.8rem;
  display: flex;
  flex-direction: column;
}

.data-block__content-row-title {
  font-weight: bold;
  width: 100%;
  flex-shrink: 0;
}

.data-block__content-list {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 768px) {
  .data-block__content-row {
    font-size: 1rem;

    flex-direction: row;
  }
  .data-block__content-row-title {
    width: 30%;
  }
  .data-block__title {
    font-size: 1.5rem;
  }
}

/* source: https://github.com/loadingio/css-spinner/ */

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid black;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

.loading-screen {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 4rem;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 1rem;
  z-index: 1000;
  width: 80%;
  height: 28rem;
  border-radius: 1rem;
  overflow: scroll;
}

.modal__button {
  background-image: linear-gradient(
    to right,
    #ff4e50 0%,
    #f9d423 51%,
    #ff4e50 100%
  );
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 20px #eee;
  border: 1px solid #ced4da;
  width: 5rem;
  height: 2rem;
  cursor: pointer;
  border-radius: 0.5rem;
  font-size: 1rem;
  opacity: 0.9;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: 0.5rem;
}

.modal__button:hover {
  background-position: right center;
  color: #222;
}

.modal--greyjoy {
  background-color: #251a06;
  color: #fed250;
}

.modal--tully {
  background-color: #85012a;
  color: #002b4e;
}

.modal--baratheon {
  background-color: #fdb814;
  color: #181818;
}

.modal--lannister {
  background-color: #a30b32;
  color: #fcde70;
}

.modal--stark {
  background-color: #fbf6cc;
  color: #120f15;
}

.modal--targaryen {
  background-color: #251a06;
  color: #a30b32;
}

.modal--arryn {
  background-color: #002b4e;
  color: #fbf6cc;
}

.modal--martell {
  background-color: #f78f1e;
  color: #8a252a;
}

.modal--tyrell {
  background-color: #94b695;
  color: #1d191a;
}

@media screen and (min-width: 768px) {
  .modal {
    padding: 2rem;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 60%;
    height: 38rem;
    padding: 3rem;
  }
}

@media screen and (min-width: 1440px) {
  .modal {
    width: 35%;
  }
}

